
.catalog__list {
  display: flex;
  justify-content: space-between;
  min-width: 100%;
}


.catalog__control {
  box-sizing: border-box;
  min-width: 200px;
  margin-top: 30px;
  background-color: rgb(189, 223, 211);
  color: rgb(10, 48, 29);
  font-size: 28px;
  padding: 4px 9px;
  cursor: pointer;
  border: none;
  border-radius: 3px;
  box-shadow: 0px -20px 16px -16px rgba(34, 60, 80, 0.14) inset;;
  transition: .3s;
  user-select: none;
}

.catalog__control:hover {
  background-color:  rgb(48, 68, 58);
  color: rgb(255, 255, 255);
}

.catalog__control:active {
  opacity: 0.7;
}

.catalog__control:disabled {
  opacity: 0.5;
  cursor: auto;
}

.catalog__control:disabled:hover {
  background-color: rgb(189, 223, 211);
  color: rgb(10, 48, 29);
}

@media (max-width: 999px) {
  .catalog__control {
    min-width: 130px;
    padding: 4px 15px;
  }
}